.results {
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.results__content{
    width: 100%;
    max-width: 800px;
    height: 100%;
    min-height: 500px;
    padding: 24px 40px;
    box-shadow: 0px 8px 16px 0px #E6EBF5;
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 24px;
    background-color: white;
  }
  
  .results__title-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .results__title-container div {
    width: 100%;
    display: flex;
    align-items: baseline;
    gap: 16px;
  }
  
  .result__title {
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 40px;
  }
  
  .results__date {
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 16px;
  }
  
  .results__open-button {
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 16px; /* 114.286% */
    color: linear-gradient(90deg, #8341EF 0%, #3F68F9 100%);
    border: none;
    padding: 0;
    margin: 0;
    text-align: end;
    background: none;
    width: 100%;
    cursor: pointer;
  }
  
  .results__text {
    padding: 0;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    resize: none;
    border: none;
  }
  
  .results__text:active {
    border: none;
  }
  
  .results__text:focus {
    border: none;
  }
  
  .results__text:hover {
    border: none;
  }
  
  .results__buttons-container {
    display: flex;
    justify-content: center;
    gap: 80px;
  }
  
  .results__option-button {
    background: none;
    border: none;
    cursor: pointer;
  }

  .results__transform-buttons-container {
    border-radius: 0px 0px 40px 40px;
    background-color: rgba(33, 32, 31, 1);
    width: 100%;
    max-width: 720px;
    padding: 15px 0;
    display: flex;
    justify-content: center;
    gap: 24px;
  }

  .results__transform-button {
    padding: 12px 8px;
    background: none;
    border: none;
    background-color: rgba(255, 255, 255, 0.12);
    border-radius: 12px;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    color: white;
    cursor: pointer;
  }